import React, { useState, useEffect } from 'react';
import CryptoJS from 'crypto-js';
import { useParams } from 'react-router-dom';
import { db, doc, getDoc } from './firebase'; // Firebase 설정 파일 경로

const App = () => {
  const [secretKey, setSecretKey] = useState('your-secret-key'); // 시크릿 키
  const [decryptedUid, setDecryptedUid] = useState('');
  const [documentData, setDocumentData] = useState(null); // Firestore에서 가져온 문서 데이터
  const { encryptedUid } = useParams(); // URL 파라미터에서 암호화된 값 받기

  useEffect(() => {
    if (encryptedUid) {
      const decodedEncryptedUid = decodeURIComponent(encryptedUid);
      decryptUid(decodedEncryptedUid);
    }
  }, [encryptedUid]);

  // 복호화 함수
  const decryptUid = (encryptedValue) => {
    const currentTime = new Date().toISOString(); // 현재 시간
    try {
      const bytes = CryptoJS.AES.decrypt(encryptedValue, secretKey);
      const decryptedData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));

      const decryptedTimestamp = new Date(decryptedData.timestamp);
      const currentTimestamp = new Date();
      const timeDiff = Math.abs(currentTimestamp - decryptedTimestamp);
      const oneHour = 60 * 60 * 1000; // 1시간을 밀리초로 변환

      if (timeDiff <= oneHour) {
        setDecryptedUid(decryptedData.uid);
        fetchDocument(decryptedData.uid); // UID로 Firestore에서 문서 조회
      } else {
        setDecryptedUid('타임스탬프 오차 초과');
      }
    } catch (error) {
      setDecryptedUid('복호화 오류');
    }
  };

  // Firestore에서 문서 조회
  const fetchDocument = async (uid) => {
    try {
      const docRef = doc(db, 'users', uid); // 문서 ID가 UID와 일치
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        setDocumentData(docSnap.data());
      } else {
        setDocumentData(null);
      }
    } catch (error) {
      console.error('Firestore 조회 오류:', error);
    }
  };

  return (
    <div>
      <h1>UID 복호화 및 Firestore 조회</h1>
      <p><strong>암호화된 UID:</strong> {decodeURIComponent(encryptedUid)}</p>
      <p><strong>복호화된 UID:</strong> {decryptedUid}</p>
      {documentData && (
        <div>
          <h2>문서 데이터</h2>
          <pre>{JSON.stringify(documentData, null, 2)}</pre>
        </div>
      )}
      {!documentData && decryptedUid && decryptedUid !== '타임스탬프 오차 초과' && <p>문서가 존재하지 않습니다.</p>}
    </div>
  );
};

export default App;
